import { Routes, Route, Navigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { message } from 'antd';
import axios from "axios";

import Login from './pages/Login';
import Signup from './pages/Signup';
import RoutesConfig from './layouts/RoutesConfig';

function App() {
	const [user, setUser] = useState(null);

  useEffect(() => { getUser(); }, []);

	const getUser = async () => {
		try {
			const url = process.env.REACT_APP_API_URL + '/api/google/auth/login/success';
			const respData  = await axios.get(url, { withCredentials: true });
			if(respData.data.statusCode == 200) setUser(respData.data.data);
			if(respData.data.statusCode == 404) message.error('Dang!!! Error occrured while Signing you in perhaps you are not a registered user. Please consider Signing Up', 15)
		} catch (err) {
			console.log(err);
		}
	};

	return (<>
    <Routes>
      <Route path="/login" element={<Login/>}/>
	    <Route path="/signup" element={<Signup/>}/>
      <Route path="/" element={<Navigate to="/login" replace />} />
    </Routes>
	  <RoutesConfig />
  </>);
}

export default App;